<template>
  <v-app >
        <keyboard-handler @setting="$refs.toolbar.toggle()" @tap="$ref.control.tap()"/>
        <toolbar ref="toolbar"/>
      <dot-display/>
  
    <v-footer>
      <control-bar ref="control" :class="`${background} use-background `"/>
    </v-footer>
    
 
  </v-app>
</template>

<script>
import KeyboardHandler from './components/KeyboardHandler.vue';
import Toolbar from './components/Toolbar';
import DotDisplay from './components/DotDisplay'
import ControlBar from './components/ControlBar'

export default {
  name: 'App',

  components: {
    Toolbar,
    KeyboardHandler,
    DotDisplay,
    ControlBar
  },

  data: () => ({
    //
  }),
  computed:{


    background(){
      if (this.$store.state.play){
      if(this.$stereoType({between: [140, 170], is:6/8})) return 'blackmetal'
      if(this.$stereoType({less: [2], is:4/1})) return 'trash'
      if(this.$stereoType({between: [150, 185], is:4/8})) return 'punk'
      if(this.$stereoType({between: [60, 90], is:4/4})) return 'tubbies'
      if(this.$stereoType({between: [90, 120], is:3/4})) return 'country'
      if(this.$stereoType({between: [80, 90], is:7/8})) return 'doom'
      if(this.$stereoType({between: [130, 150], like:[9/8, 10/8]})) return 'metal'
      if(this.$stereoType({between: [110, 135], like:[7/4, 11/8, 13/8, 15/16]})) return 'mathrock'
      if(this.$stereoType({between: [200, 240], is:6/8})) return 'pornogrind'
      if(this.$stereoType({more: 240, like:[5/4, 8/8]})) return 'grindcore'
      }
      return ''
    }

  },
  methods:{

  },

  mounted(){

  }
};
</script>

<style>

.blackmetal{
  background : url('./assets/typical/black.svg');
}
.country{
  background: url('./assets/typical/country.svg');
}
.doom{
  background: url('./assets/typical/doom.svg');
}
.grindcore{
  background : url('./assets/typical/grindcore.svg');
}
.mathrock{
  background: url('./assets/typical/mathrock.svg');
}
.metal{
  background: url('./assets/typical/metal.svg');
}
.pornogrind{
  background: url('./assets/typical/pornogrind.svg');
}
.punk{
  background: url('./assets/typical/punk.svg');
}
.trash{
  background: url('./assets/typical/trash.svg');
}
.tubbies{
  background: url('./assets/typical/tubbies.svg');
}





.use-background{
  /* background-repeat: no-repeat; */

  background-size: 100% 100%;
}
</style>
