<template>
  <div class="ma-1">
<!-- 
                <v-progress-circular :size="size+10" value="100" width="5" @click="changeType" :color="$store.state.ringColor"
                class="v-avatar--metronome"
                v-if="playing"
                :style="{
                            animationDuration: animationDuration
                        }"
                >
                <v-avatar
                    :size="size"
                    :color="`${color}`"
                    class="visible"
                >
                </v-avatar>
                </v-progress-circular> -->

                <v-progress-circular :size="size+10" value="100" width="5" @click="changeType" :color="$store.state.ringColor"
                   class="invisible" 
                   ref="ring"
                    >
                                <v-avatar
                                    :size="size"
                                    :color="`${color}`"
                                    class="visible"

                                    
                                > 
                                <span :class="`${$store.state.ringColor}--text text--lighten-2 display-2 invisible`" 
                                v-html="$store.state.beatEntities[$store.state.currentBeatType]"
                                ref="beat"></span>
                </v-avatar>
                </v-progress-circular>


            


      <!-- <v-progress-circular :size="size+10" value="100" width="5" @click="changeType" :color="$store.state.ringColor"
      :class="isPlaying">
      <v-avatar
          :size="size"
          :color="`${color}`"
          class="visible"
      >
      </v-avatar>
      </v-progress-circular> -->
      <audio :src="audio" class="invisible" ref="sound"></audio>
      
      
  </div>
</template>

<script>
export default {
    data(){
        let accent = this.accent
        return{
            vol: this.volume,
            acc: accent,
            playing: false,
            dur: 0,
            src: undefined,
            pr: 1
        }
    },
    props:{
        accent:{
            type: Boolean,
            default: false
        },
        volume:{
            type: Number,
            default: 70,
        }
    },
    computed:{
        color(){
            if (this.acc){
                return this.$store.state.accentColor
            }
            return this.$store.state.beatColor
        },
        
        audio(){
            return this.acc ? this.$store.getters.get_accent_audio_source : this.$store.getters.get_beat_audio_source
        },

        
        size(){
            return 50
        },

        animationDuration () {
            return `${this.$playInterval()/1000}s`
        },

        tempo(){
            return this.$playInterval()
        }

    },
    methods:{
        isPlaying(x){
            if(x){
                return 'visible'
            }
            return 'invisible'
        },

        changeType(){
            this.acc=!this.acc
        },

        play(){
            this.display(true)
            this.$refs.sound.play()
        },

        // stop(time){
        //     setTimeout(()=>{
        //         this.display(false)
        //         if(this.$refs.sound){
        //         this.$refs.sound.pause()
        //         this.$refs.sound.currentTime = 0
        //         }
        //     },time)
            
        // },

        display(s){
            this.playing = s
        },
    },

    mounted(){
        this.$nextTick(function(){
            let v = this
            this.$refs.sound.oncanplay = function(e){
                v.src = e.target.src
                v.pr = e.target.playbackRate
            }
            // this.$refs.sound.onended = function(){
            //     v.display(false)
            // }
            this.$refs.sound.onplay = function(e){
                v.$refs.ring.$el.classList.remove('invisible')
                v.$refs.beat.classList.remove('invisible')
                setTimeout(function () {
                    v.$refs.ring.$el.classList.add('invisible')
                    v.$refs.beat.classList.add('invisible')

                    e.target.pause()
                    e.target.currentTime = 0
                }, v.$stopInterval())
                
                
            }
        })

    },

    watch:{

        src(){
            try {
                let duration = this.$refs.sound.duration*1000/ this.$playInterval();
                if(duration<1){
                    duration = 1
                }
                // duration+=duration*10/100
                this.$refs.sound.playbackRate =  duration.toFixed(1)
                
            } catch (error) {
                return
            }
            
        },
        tempo(){
            let duration = this.$refs.sound.duration*1000/ this.$playInterval();
            if(duration<1){
                    duration = 1
                }
            this.$refs.sound.playbackRate =  duration.toFixed(1)
        },
        // pr(val){
        //     // console.log(val);
        // }
        
    }

}
</script>

<style scooped>
.invisible{
    visibility: hidden;
}
.visible{
    visibility: visible;
    opacity: 1;
}
  @keyframes in-fade {
    from {
      visibility: hidden;
    }

    to {
      visibility: visible;
    }
  }
@keyframes out-fade {
    0% {
opacity: 0;    
}

    100% {
    opacity: 1;
    visibility: hidden;
    }
    
  }

  .v-out {
    animation-name: out-fade;
    animation-iteration-count: infinite;
    animation-direction:reverse;
  }


</style>