<template>
  <v-row>
      <v-col cols="6">
                <v-subheader>
                    {{text}}
                  </v-subheader>
                </v-col>

              <v-col cols="6">
                <v-select
                  v-model="select"
                  :items="list"

                  :label="select"
                  persistent-hint
                  single-line
                  @change="change"
                ></v-select>
              </v-col>
  </v-row>
</template>

<script>
export default {
    data(){
        let accent = this.accent
        let model = accent ? 
            this.$store.state.audioSource[this.$store.state.accent] : 
            this.$store.state.audioSource[this.$store.state.beat]
        return{
            // item: accent ? this.$store.state.audio.accent : this.$store.state.audio.beat,
            item: this.$store.state.audioSource,
            select: model.replace(model[0], model[0].toUpperCase()),
            text: accent ? 'Accent Sound' : 'Beat Sound'
        }
    },
    props:{
        accent:{
            type: Boolean,
            default: false
        }
    },
    computed:{
        list(){
            let item = this.item
            let list = []
            item.forEach(i => {
                list.push(i.replace(i[0], i[0].toUpperCase()))
            });
            return list
        }
    },
    methods:{
        change(){
            let x = this.item.indexOf(this.select.toLowerCase())
            this.accent ? this.$store.commit('changeAudioAccent', x) : this.$store.commit('changeAudioBeat', x)
        }
    },


}
</script>

